.nav-header {
    height: 25vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: url('https://assets.ccbp.in/frontend/react-js/books-blog-bg.png');
  }
  .nav-menu {
    list-style-type: none;
    display: flex;
    margin-left: -40px;
  }
  
  .nav-link {
    text-decoration: none;
    margin: 10px;
    color: white;
    font-family: 'Roboto';
  }
  
  .blog-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .blog-title {
    font-family: 'Roboto';
    color: white;
    font-size: 16px;
  }
  .sub-title {
    color: white;
    font-family: 'Roboto';
    margin-top: 0px;
  }
  
  li {
    list-style-type: none;
    text-decoration: none;
    margin: 10px;
    color: #ffffff;
    font-family: 'Roboto';
    cursor: pointer;
  }
  
  @media screen and (min-width: 575px) {
    .blog-title {
      font-family: 'Roboto';
      color: white;
      font-size: 34px;
    }
  }
  .link{
    color: #ffffff;
    font-weight: 500;
    margin: 10px;
    font-size: 20px;
  }